.mask{
    width: 100%;
    height: 100vh;
    position: relative;
    
}
.into-img{
    position: absolute;
    width: 50%;
    height: 80%;
    left: 50%;
    top: 10%;
    background-repeat: no-repeat;
    background-attachment: fixed;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  
} 

  .hero{
    height: 100%;
    width: 100%;
}  
.content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 30%;
    text-align: center;
}
.content h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}
.content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.5rem;
 
}
@media screen and (max-width:640px) {
    .content{
        top:90%;
        left: 50%;
    }
    .into-img{
        position: absolute;
        top: -10%;
        left: 30%;
        height: 70%;
    }
    .content h1{
        font-size:3rem;

    }
    .content p{
font-size: 1.4rem;
    }

}