.main-container{
    display: flex; flex-direction: row; justify-content: space-around;
}
.boxx{
display: flex;
height: 300px;
width:250px;
background-color: white;
box-sizing: border-box;
border: 1px solid white;
border-radius: 40px;
box-shadow: 12px 12px 6px lightblue;
transition: transform .2s;
}
.boxx:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    transform: scale(1.1);
  }
  .box-style{
    display: flex;
  }
  .box-style img{
    width: 150%;
    height: 40%;
    top: 10px;
    left: 10px;
  }
img{
    position :relative
}
