
.heading{
    width: 100%;
    height: 100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.hero-img h1{
    font-size:2.4rem

}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
    top: 5%;
    position: relative;
}
.resume{
    padding: 20px 30px;
    font-size : 1rem;
    text-transform: uppercase;
    background:grey;
    color: #222;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
    border-radius: 20px;
   position: absolute;
   left : 90%;
   bottom: 20%;
   width: 30%;
   text-align: left;
  
}

@media  screen and (max-width:640px) {
    .hero-img h1{
        font-size :2rem;
    }
    
}