display: flex;
height: 400px;
width:300px;
background-color: grey;
box-sizing: border-box;
border: 1px solid white;
border-radius: 5px;
box-shadow: 12px 12px 6px lightblue;
transition: transform .2s;

}
img{
  width: 100%;
  height: 50%;
  background-size: cover;
    
}
 p{
  color: white;
  
}
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  transform: scale(1.1);
}


.container {
  padding: 2px 16px;
}
.main-card{
  display: flex;
  flex-direction: row;
  justify-content: space-around;

 
}
.headg{
  text-align: center;
padding: 5rem 0.5rem;
}