@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;400&display=swap');
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}
body{
  background : black;

}
h1,h4,p,a{
  color :#fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}

.btn{
  padding: 12px 32px;
  font-size : 1rem;
  text-transform: uppercase;
  background: rgb(248,217,15);
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;

}

.btn-light{
  background: transparent;
  color: #fff;
}
.btn:hover{
  background: rgba(255,255,255,0.2);
  color: #fff;
  transition: 0.3s;
}
.hero-img{
  width: 100%;
  background: rgba(0,0,0,0.5);
  height: 100vh;
  position: relative;
}

.hero-img ::before{
  content: "";
  background: url("../src/assets/img1.jpeg");
  background-size: cover;
  background-position: center top;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.pop {
  width: 60%;
  height: 60%;
  min-width: 40rem;
  padding: 4rem 3rem;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 31px;
  transition: all 1.5s;
  background: linear-gradient(1300deg, #938046 40%, #978b1f 60%);
  ;
  box-shadow: 15px 15px 25px #b9b9b9, -15px -15px 25px #b9b9b9;
  
  
}
