
 

 .content-img{
    width: 100%;
    background: rgba(0,0,0,0.5);
    height: 100vh;
    position: relative;
} 

.content-img::before{
    content: "";
    background: url("../assets/img1.jpeg");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
